<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="butiemingcheng">
				<el-input v-model="ruleForm.butiemingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.butiemingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.butiefengmian)" prop="butiefengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.butiefengmian?ruleForm.butiefengmian:''"
					@change="butiefengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="butiefengmian">
				<img v-if="ruleForm.butiefengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.butiefengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.butiefengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="butieziliao">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="3"
					:multiple="true"
					:fileUrls="ruleForm.butieziliao?ruleForm.butieziliao:''"
					@change="butieziliaoUploadChange"
					></file-upload>
			</el-form-item>  
			<el-form-item class="add-item" label="" prop="butiejine">
				<el-input-number v-model="ruleForm.butiejine" placeholder="" :disabled=" false ||ro.butiejine"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="shenqingzhuangtai">
				<el-select v-model="ruleForm.shenqingzhuangtai" placeholder="" :disabled="true  ||ro.shenqingzhuangtai" >
					<el-option
						v-for="(item,index) in shenqingzhuangtaiOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shenqingshijian">
				<el-date-picker
					:disabled=" false  ||ro.shenqingshijian"
					format="yyyy  MM  dd "
					value-format="yyyy-MM-dd"
					v-model="ruleForm.shenqingshijian" 
					type="date"
					placeholder="">
				</el-date-picker> 
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shenqingneirong">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.shenqingneirong">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shenqingxiangqing">
				<editor 
					v-model="ruleForm.shenqingxiangqing" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					butiemingcheng : false,
					butiefengmian : false,
					butieziliao : false,
					butiejine : false,
					shenqingzhuangtai : false,
					shenqingshijian : false,
					shenqingneirong : false,
					shenqingxiangqing : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					butiemingcheng: '',
					butiefengmian: '',
					butieziliao: '',
					butiejine: '',
					shenqingzhuangtai: '' ,
					shenqingshijian: '',
					shenqingneirong: '',
					shenqingxiangqing: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
				},
				shenqingzhuangtaiOptions: [],


				rules: {
					butiemingcheng: [
					],
					butiefengmian: [
					],
					butieziliao: [
					],
					butiejine: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					shenqingzhuangtai: [
					],
					shenqingshijian: [
					],
					shenqingneirong: [
					],
					shenqingxiangqing: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.shenqingshijian = this.getCurDate()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='butiemingcheng'){
							this.ruleForm.butiemingcheng = obj[o];
							this.ro.butiemingcheng = true;
							continue;
						}
						if(o=='butiefengmian'){
							this.ruleForm.butiefengmian = obj[o].split(",")[0];
							this.ro.butiefengmian = true;
							continue;
						}
						if(o=='butieziliao'){
							this.ruleForm.butieziliao = obj[o];
							this.ro.butieziliao = true;
							continue;
						}
						if(o=='butiejine'){
							this.ruleForm.butiejine = obj[o];
							this.ro.butiejine = true;
							continue;
						}
						if(o=='shenqingzhuangtai'){
							this.ruleForm.shenqingzhuangtai = obj[o];
							this.ro.shenqingzhuangtai = true;
							continue;
						}
						if(o=='shenqingshijian'){
							this.ruleForm.shenqingshijian = obj[o];
							this.ro.shenqingshijian = true;
							continue;
						}
						if(o=='shenqingneirong'){
							this.ruleForm.shenqingneirong = obj[o];
							this.ro.shenqingneirong = true;
							continue;
						}
						if(o=='shenqingxiangqing'){
							this.ruleForm.shenqingxiangqing = obj[o];
							this.ro.shenqingxiangqing = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
					}
				});
				this.shenqingzhuangtaiOptions = ",".split(',')

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`shenqingbutie/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`shenqingbutie/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			butiefengmianUploadChange(fileUrls) {
				this.ruleForm.butiefengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			butieziliaoUploadChange(fileUrls) {
				this.ruleForm.butieziliao = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

